import React, {useEffect, useState} from "react";
import { dbService } from "../fbase";
import Nweet from "../components/Nweet";
import NweetFactory from "../components/NweetFactory";
import { Link } from "react-router-dom";
import icon5 from "../components/icons/icon5.jpg";
import Home from "../components/icons/home.jpg";



const Diary =  ({userObj}) => {
    const [nweets, setNweets] = useState([]);
    useEffect(() => {
        dbService.collection("nweets").onSnapshot(snapshot => {
            const nweetArray = snapshot.docs.map(doc => ({
                id:doc.id,
                ...doc.data(),
            }));
            setNweets(nweetArray);
        });
    }, [])
    
    return(
      <div className="page">
        <div className="pageTitleBack">
          <div className="pageTitle">
            <div className="in">
              <div style={{float: "left"}}><img width="100px" height="100px" src={icon5}></img></div>
              <div style={{float: "left", textAlign:"center", marginTop:"40px"}}>사구 식물 관찰 기록장</div>
            </div>
          </div>
        </div>
        <div className="pageExplain">나의 관찰기록 등록하기</div>
        <div className="container">
          <NweetFactory userObj={userObj} />
            <div style={{ marginTop: 30 }}>
                {nweets.map(nweet => (
                    <Nweet key={nweet.id} nweetObj={nweet} isOwner={nweet.creatorId === userObj.uid}/>
                ))}
            </div>
        </div>
        <nav>
          <ul style={{display: "flex", justifyContent: "left", marginTop: 30, marginLeft: 30, width: "100%" }}>
            <li>
              <Link to="/" style={{ 
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    fontSize: 12,}}>
                    <img width="50px" height="50px" src={Home}></img>
                    <span style={{ marginTop: 10 }}>홈</span>
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    );
};
export default Diary;